@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    background: rgba(0, 0, 0, 0.03);
}
.search-bar{
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 10px 0;
}
.video-con{
    display: flex;
    flex-direction: row;
}
.video-con .video-detail{
    padding: 10px 0 0 0;
}
.video-list{
    display: flex;
    flex-direction: column;
}
.video-list .video-item{
    display: flex;
    width: 100%;
    border-bottom: 1px solid #ccc;
    padding: 10px 0;
    cursor: pointer;
}
.video-list .video-item .desc{
    display: flex;
    flex-direction: column;
}
    .video-list .video-item img{
    margin: 0 1em 0 0;
    width: 150px;
}
.video-list .video-item .desc h6{
    font-size: 14px;
    font-weight: 500;
    max-height: 30px;
    overflow: hidden;
}
.video-list .video-item .desc p{
    font-size: 13px;
    color: rgba(0, 0, 0, 0.6);
    margin: 0 0 0 0;
    line-height: normal;
}
